
















































form {
  button {
    width: 100px;
    height: 40px;
    align-self: flex-end;
    padding: 8px;
    margin: 8px;
  }
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 30px 30px 30px;
  .form-title {
    display: block;
    text-align: center;
  }
  div {
    align-items: center;
    display: flex;
    justify-content: left;
    div {
      padding: 10px;
    }
  }
}
form > div {
  width: 100%
}
